.container {
    display: flex;
  margin-left: 50px;
  margin-top: 10px;
  width: max-content;
  transform: translateX(0px);
  transition: all 1s ease;
}


/* Laptops */
@media (min-width: 680px) {

    .list {
      width: 100%;
      margin-top: 10px;
      overflow: hidden;
    }
    
    .wrapper {
      position: relative;
      overflow: visible;
    }
    
    .sliderArrow {
      width: 50px;
      height: 100%;
      background-color: rgb(22, 22, 22, 0.5);
      color: white;
      position: absolute;
      z-index: 99;
      top: 0;
      bottom: 0;
      margin: auto;
      cursor: pointer;
    }
    
    .left {
      left: 0;
    }
    
    .right {
      right: 0;
    }
    

}



